:root {
  --app-height: 100%;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  width: 100vw;
}

@media not all and (hover:hover) {
  body {
    height: var(--app-height);
  }
}

* {
  box-sizing: border-box;
}

#app {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(orange, yellow);
}
